import { LinkGetProps } from '@reach/router'

export const isMenuActive =  (isMenuActiveProps: LinkGetProps) => {
  const { isPartiallyCurrent } = isMenuActiveProps
  const className = [
    'menu-link',
    isPartiallyCurrent && 'active'
  ].filter(Boolean).join(' ')
  return { className }
}

export const MENUS = [
  { to: '/blog', text: '' },
  { to: '/project', text: '' },
  { to: '/toolbox', text: '' },
  { to: '/friend', text: '' },
  { to: '/about', text: '' }
]
